<template>
<div>
  <el-menu background-color="#545c64" text-color="#ffd04b" active-text-color="#ffd04b" mode="horizontal" >
    <el-menu-item index="1">Kubernetes-Etcd-Browser</el-menu-item>
  </el-menu>
  <el-row style="height:100%">
    <el-col :span="3">
      <div class="ns-title">
        <p class="ns-title-span">当前命名空间</p>
      </div>
      <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick" :highlight-current="true"></el-tree>
    </el-col>
    <el-col :span="21">
        <el-tabs v-model="activeName" type="border-card" @tab-click="tabClick" size="mini">
          <el-tab-pane label="Deployment" name="deployment">
              <v-deployment :data="deployments" @setSource="setSource"></v-deployment>
          </el-tab-pane>
          <el-tab-pane label="Daemonset" name="daemonset">
              <v-daemonset :data="daemonsets" @setSource="setSource"></v-daemonset>
          </el-tab-pane>
          <el-tab-pane label="Pod" name="pod">
              <v-pod :data="pods" @setSource="setSource"></v-pod>
          </el-tab-pane>
          <el-tab-pane label="Service" name="service">
            <v-service :data="services" @setSource="setSource"></v-service>
          </el-tab-pane>
          <el-tab-pane label="Ingress" name="ingress">
            <v-ingress :data="ingresses" @setSource="setSource"></v-ingress>
          </el-tab-pane>
        </el-tabs>
    </el-col>
  </el-row>
  <v-view :data="jsonSource" :visible="dialogVisible" @close="close"></v-view>
</div>
</template>
<script>
  import VView from  '@/components/VView'
  import VDeployment from '@/components/VDeployment'
  import VDaemonset from '@/components/VDaemonset'
  import VPod from '@/components/VPod'
  import VService from '@/components/VService'
  import VIngress from '@/components/VIngress'
  export default {
    name:'app',
    components: {VView,VDeployment,VDaemonset,VPod,VService,VIngress},
    data() {
      return {
        namespace:"default",
        kind:"deployment",
        deployments:[],
        daemonsets:[],
        pods:[],
        services:[],
        ingresses:[],
        dialogVisible:false,
        activeName:"deployment",
        jsonSource:{},
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        data:[]
      };
    },
    methods: {
      queryNamespacce(){
        var vm = this;
        let url = "/namespace";
        vm.isLoading = true;
        vm.$axios.get(url)
          .then(function (res) {
              vm.data = res.data;
          })
      },
      queryKind(){
        var vm = this;
        let url = "/"+vm.kind;
        let param = {
          namespace:vm.namespace
        }
        vm.$axios.post(url,param)
          .then(function (res) {
            switch(vm.kind)
            {
              case 'deployment':
                vm.deployments = res.data 
                break;
              case 'daemonset':
                vm.daemonsets = res.data
                break;
              case 'pod':
                vm.pods = res.data
                break;
              case 'service':
                vm.services = res.data
                break;
              case 'ingress':
                vm.ingresses = res.data
                break;
              default:
            }
        })
      },
      setSource(source){
        console.log(source)
        this.dialogVisible = true;
        this.jsonSource = source;
      },
      close(){
        this.dialogVisible = false;
      },
      remove(node, data) {
        const parent = node.parent;
        const children = parent.data.children || parent.data;
        const index = children.findIndex(d => d.id === data.id);
        children.splice(index, 1);
      },
      handleNodeClick(node) {
        this.jsonSource = ""
        this.jsonSource = node.data
        this.namespace = node.label
      },
      tabClick(tab, event){
        this.kind = tab.name
      }
    },
    watch: {
		  namespace(newNamespace) { 
        this.queryKind()
		  },
      kind(newKind) { 
        this.queryKind()
		  }
    },
    mounted:function(){
      this.queryNamespacce()
      this.queryKind()
    }
  }
</script>
<style lang="scss" scoped>
.ns-title {
    width: 100%;
    height: 50px;
    background-color: #f1f1f1;
    text-align: center;
    vertical-align: middle;
}
.ns-title-span{
   padding-top: 10px;
   vertical-align: middle;
   font-size: 20px;
   font-weight: bold;
   margin: 0px;
}

</style>



