<template>
<el-table :data="data" height="600" border  style="width: 100%">
    <el-table-column prop="metadata.name"   label="NAME" width="150":show-overflow-tooltip="true"></el-table-column>
    <el-table-column prop="status.desiredNumberScheduled"   label="DESIRED" width="80"></el-table-column>
    <el-table-column prop="status.currentNumberScheduled"   label="CURRENT" width="85"></el-table-column>
    <el-table-column prop="status.numberReady"   label="READY" width="80"></el-table-column>
    <el-table-column prop="status.updatedNumberScheduled"   label="UP-TO-DATE" width="120"></el-table-column>
    <el-table-column prop="status.numberAvailable"   label="AVAILABLE" width="100"></el-table-column>
    <el-table-column prop="nodeselect"   label="NODE SELECTOR" width="150" 
    :show-overflow-tooltip="true">
        <template slot-scope="scope">
        <div v-if="scope.row.spec.template.spec.hostNetwork" >
            {{scope.row.spec.template.spec.nodeSelector}} 
        </div>
        </template>
    </el-table-column>

    <el-table-column prop="creationTimestamp"   label="AGE" width="80">
        <template slot-scope="scope">
        <div v-if="scope.row.metadata.creationTimestamp" >
            {{formatAge(scope.row.metadata.creationTimestamp)}} 
        </div>
        </template>
    </el-table-column>
    <el-table-column prop="spec.template.spec.containers[0].name"   label="CONTAINERS" width="150"
    :show-overflow-tooltip="true"></el-table-column>
    <el-table-column prop="spec.template.spec.containers[0].image"   label="IMAGES" width="150"
    :show-overflow-tooltip="true">
    </el-table-column>
    <el-table-column prop="SELECTOR" label="SELECTOR" width="150"
    :show-overflow-tooltip="true">
        <template slot-scope="scope">
        <div v-if="scope.row.spec.selector.matchLabels" >
            {{scope.row.spec.selector.matchLabels}} 
        </div>
        </template>
    </el-table-column>
    <el-table-column prop="operation" label="operation" width="150"  fixed="right">
        <template slot-scope="scope">
        <div >
            <el-button type="text" @click.native="describe(scope.row)">describe</el-button>
        </div>
        </template>
    </el-table-column>
</el-table>
</template>

<script>
  export default {
    name: "v-daemonset",
    props: {
      data: Array
    },
    methods: {
     formatAge(creationTimestamp){
        var now = new Date();
        var seperator1 = "-";
        var month = now.getMonth() + 1;
        var strDate = now.getDate();
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        var currentDate = now.getFullYear() + seperator1 + month + seperator1 + strDate

        var index = creationTimestamp.indexOf("T")
        var createDate = creationTimestamp.substring(0,index)
        //console.log(now.getFullYear())
        var result = (new Date(currentDate)-new Date(createDate))/(1000 * 60 * 60 * 24)
        return result+"d"
      },
      describe(data){
        this.$emit('setSource', data);
      }
    }
  }
</script>

<style lang="scss" scoped>
  
</style>




