<template>    
    <el-dialog top="10vh" title="详情" :visible.sync="visible" :before-close="handleClose">
        <tree-view :data="data" :options="{maxDepth: 1}" style="padding-bottom: 20px"></tree-view>
    </el-dialog>
</template>

<script>
  export default {
    name: "v-view",
    props: {
      data: Object,
      visible: Boolean
    },
    methods: {
        handleClose() {
		    this.$emit('close');
        }
    }
  }
</script>

<style lang="scss" scoped>
  .tree-view-item-root {
    margin-left: 0!important;
    padding-bottom: 20px;
}
</style>




