<template>
<el-table :data="data" height="600" border  style="width: 100%">
    <el-table-column prop="metadata.name"   label="NAME" width="200":show-overflow-tooltip="true"></el-table-column>
    <el-table-column prop="spec.type"   label="HOSTS" width="120"></el-table-column>
    <el-table-column prop="spec.clusterIP"   label="ADDRESS" width="120"></el-table-column>
    <el-table-column prop="status.podIP"   label="PORTS" width="180">
        <template slot-scope="scope">
            <div v-if="scope.row.spec.ports" >
                {{formatPort(scope.row.spec.ports)}} 
            </div>
        </template>
    </el-table-column>
    <el-table-column prop="creationTimestamp"   label="AGE" width="100">
        <template slot-scope="scope">
            <div v-if="scope.row.metadata.creationTimestamp" >
                {{getAge(scope.row.metadata.creationTimestamp)}} 
            </div>
        </template>
    </el-table-column>
    
    <el-table-column prop="spec.selector"  label="SELECTOR" width="200"></el-table-column>
    <el-table-column prop="operation" label="operation" width="150"  fixed="right">
        <template slot-scope="scope">
        <div >
            <el-button type="text" @click.native="describe(scope.row)">describe</el-button>
        </div>
        </template>
    </el-table-column>
</el-table>
</template>

<script>
  export default {
    name: "v-daemonset",
    props: {
      data: Array
    },
    methods: {
     getAge(creationTimestamp){
        var now = new Date();
        var seperator1 = "-";
        var month = now.getMonth() + 1;
        var strDate = now.getDate();
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        var currentDate = now.getFullYear() + seperator1 + month + seperator1 + strDate

        var index = creationTimestamp.indexOf("T")
        var createDate = creationTimestamp.substring(0,index)
        var result = (new Date(currentDate)-new Date(createDate))/(1000 * 60 * 60 * 24)
        return result+"d"
      },
      formatPort(ports){
        var str = "";
        ports.forEach(function(element) {
            if(str!=""){
                str =  str + ","+ element.port +":"+element.nodePort+"/"+element.protocol
            }
        }, this);
        return str
      },
      describe(data){
        this.$emit('setSource', data);
      }
    }
  }
</script>

<style lang="scss" scoped>
  
</style>




